.container {
    background: #fff;
    padding: 30px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    width: 450px;
}

h2 {
    margin-bottom: 15px;
    font-size: 22px;
    font-weight: bold;
    text-align: left;
    color: #333;
    border-bottom: 2px solid #333;
    display: inline-block;
}

.form-group {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
}

.form-group label {
    width: 48%;
    font-size: 14px;
    font-weight: bold;
    color: #333;
}

.form-group input {
    width: 48%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 14px;
    outline: none;
}

.gender {
    display: flex;
    flex-direction: column;
    margin-bottom: 15px;
}

.gender label {
    font-size: 14px;
    font-weight: bold;
    color: #333;
    margin-bottom: 5px;
}

.gender-options {
    display: flex;
    gap: 10px;
    align-items: center;
}

.gender-options input {
    margin-right: 5px;
}

.register-btn {
    width: 100%;
    padding: 10px;
    background: #3a7bd5;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    margin-top: 10px;
}

.register-btn:hover {
    background: #295bb0;
}