/* Footer general styles */
.footer {
  background-color: #f8f8f8;
  color: #333;
  padding: 40px 50px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  position: relative;
}

.footer .column {
  flex: 1;
  min-width: 200px;
  margin-bottom: 20px;
}

.footer h3 {
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 12px;
}

.footer a {
  text-decoration: none;
  color: #333;
  display: block;
  margin-bottom: 8px;
  transition: color 0.3s ease;
}

.footer a:hover {
  color: #007bff;
}

.footer .contact p {
  margin: 5px 0;
}

.footer .social-icons a {
  font-size: 18px;
  margin-right: 12px;
  transition: color 0.3s ease;
}

.footer .social-icons a:hover {
  color: #007bff;
}

/* Copyright styling */
.copyright {
  height: 50px;
  text-align: center;
  background-color: #ddd;
  padding: 10px;
  font-size: 14px;
  margin-top: 40px; /* Adjusted to avoid overlap */
  font-weight: bold;
  width: 100%;
}

/* Connect Section styling */
.connect {
  margin-left: 0;
  margin-bottom: 10px;
}

.icons {
  display: flex;
  gap: 15px;
  margin-top: 20px;
  justify-content: center; /* Center the social icons */
}

.icons a {
  font-size: 20px;
  color: #333;
  transition: color 0.3s ease;
}

.icons a:hover {
  color: #007bff; /* Blue on hover */
}

/* Responsive Design for Mobile and Tablets */

/* For Mobile Devices */
@media (max-width: 768px) {
  .footer {
    padding: 20px; /* Reduced padding for mobile */
    flex-direction: column; /* Stack columns vertically */
    text-align: center;
  }

  .column {
    text-align: center; /* Ensure all columns are centered */
    margin-bottom: 20px;
  }

  .social-icons .icons {
    margin-top: 20px;
  }

  .footer .column {
    margin-bottom: 30px; /* More space between columns */
  }

  .footer .column a {
    font-size: 14px; /* Smaller font size for mobile */
  }

  .footer .contact p {
    font-size: 14px; /* Adjust font size for contact info */
  }

  .copyright {
    margin-top: 20px; /* More space above the copyright */
  }
}

/* For Small Mobile Screens (less than 480px) */
@media (max-width: 480px) {
  .footer .column h3 {
    font-size: 14px; /* Smaller headings on very small screens */
  }

  .footer a {
    font-size: 12px; /* Smaller links on small screens */
  }

  .footer .contact p {
    font-size: 12px; /* Adjust contact text */
  }

  .icons a {
    font-size: 16px; /* Smaller icons */
  }
}

/* For Larger Screens (Tablets and Desktops) */
@media (min-width: 1024px) {
  .footer {
    padding: 40px 80px; /* Larger padding for desktops */
  }

  .footer .column {
    min-width: 250px; /* Increase the min-width of the columns */
  }

  .social-icons .icons {
    margin-top: 0; /* Reset margin on larger screens */
  }

  .copyright {
    margin-top: 40px; /* Adjust space above copyright */
  }
}
