/* Container holding the entire About Us section */
.about-container {
  display: flex;
  flex-direction: column; /* Default to column for mobile */
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 20px;
  gap: 20px; /* Space between text and image */
}

/* Style for the text section */
.about-text {
  max-width: 800px; /* Limit text width */
  margin-bottom: 20px;
}

.about-text h1 {
  font-size: 2rem;
  margin-bottom: 15px;
  color: #333;
}

.about-text p {
  font-size: 1rem;
  line-height: 1.6;
  color: #666;
}

/* Style for the image section */
.about-image img {
  width: 800px;
  /* max-width: 500px; */
  height: 350px;
  border-radius: 10px; /* Optional: Rounded corners */
}


/* Mobile responsiveness */
@media (max-width: 768px) {
  .about-container {
    flex-direction: column; /* Stack elements vertically */
    padding: 10px;
  }

  .about-text h1 {
    font-size: 1.5rem; /* Adjust title size */
  }

  .about-text p {
    font-size: 0.95rem; /* Adjust paragraph font size */
  }

  .about-image img {
    max-width: 100%; /* Make the image responsive on smaller screens */
  }
}

/* Tablet responsiveness */
@media (max-width: 480px) {
  .about-container {
    padding: 5px;
  }

  .about-text h1 {
    font-size: 1.2rem; /* Reduce font size for mobile */
  }

  .about-text p {
    font-size: 0.9rem; /* Further reduce font size */
  }

  .about-image img {
    max-width: 100%;
  }
}

/* Optional: Desktop responsiveness */
@media (min-width: 1024px) {
  .about-container {
    flex-direction: row; /* Align text and image side by side */
    justify-content: space-between;
  }

  .about-image img {
    max-width: 40%; /* Limit image size on large screens */
  }

  .about-text {
    max-width: 50%; /* Limit text width on large screens */
  }

  .about-text h1 {
    font-size: 2.5rem; /* Larger title size for desktop */
  }

  .about-text p {
    font-size: 1.2rem; /* Increase paragraph font size for desktop */
  }
}
