.why-choose-us {
    background-color: gray;
    color: white;
    padding: 50px 20px;
    text-align: center;
  }
  
  .features-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
    max-width: 1000px;
    margin: auto;
  }
  
  .contact-container {
    /* background-color: #28a745; */
    color: white;
    text-align: center;
    padding: 60px 20px;
  }
  
  .footer {
    /* background-color: #111; */
    color: white;
    padding: 40px 20px;
  }
  
  .footer-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  
  .footer-company,
  .footer-address,
  .footer-contact {
    flex: 1;
    min-width: 250px;
  }
  
  .footer-bottom-text {
    text-align: center;
    padding-top: 10px;
  }
  
  @media (max-width: 768px) {
    .footer-container {
      flex-direction: column;
      text-align: center;
    }
    .footer-contact {
      text-align: center;
    }
  }