.contact-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  font-family: Arial, sans-serif;
  background-color: #f8f8f8;
  width: 100%;
}

/* Layout for Map and Contact Details Side-by-Side */
.map-contact-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  max-width: 1200px;
  gap: 20px;
  margin-bottom: 20px;
}

/* Map Section */
.map-section {
  flex: 1;
  height: 350px;
}

.map-section iframe {
  width: 100%;
  height: 100%;
  border: 0;
  border-radius: 5px;
}

/* Contact Details Section */
.contact-details {
  flex: 1;
  background: white;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  text-align: left;
}

.contact-details h2 {
  color: #d4a017;
  margin-bottom: 10px;
}

.contact-details p {
  font-size: 16px;
  line-height: 1.5;
}

.contact-details a {
  color: #007bff;
  text-decoration: none;
}

/* Contact Form Section */
.contact-form {
  width: 100%;
  max-width: 900px;
  padding: 20px;
  background: white;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.contact-form form {
  display: flex;
  justify-content: space-between;
  gap: 20px;
}

/* Left section for Name, Email, Number */
.form-left {
  display: flex;
  flex-direction: column;
  gap: 15px;
  width: 48%;
}

/* Right section for Message */
.form-right {
  width: 48%;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

.contact-form button {
  margin-top: 200px;
  margin-left: -877px;
  padding: 15px;
  background: #d4a017;
  border: none;
  color: white;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
}

.contact-form button:hover {
  background: #b38513;
}

/* Responsive Design */
@media (max-width: 768px) {
  .map-contact-wrapper {
    flex-direction: column;
  }

  .map-section {
    height: 250px;
  }

  .contact-details {
    text-align: center;
  }

  /* Stacking the form inputs vertically on mobile */
  .contact-form form {
    flex-direction: column;
  }

  .form-left,
  .form-right {
    width: 100%;
  }
}
